let constUrl;
export default{
	data(){
		return{
				openId:"",
				userPWord:"",
				userCode:"",
				msg:null,
				bills:null,
				user:null,
				canIUse: wx.canIUse('button.open-type.getUserInfo')
		}
	
	},
	onLoad(){
		let that=this;
		constUrl=that.$consturl;
		that.openId=uni.getStorageSync("openid");
		uni.request({
			url:"https://www.ccweb.xyz/gas/wx/api/getUserinfo",
			data:{
				openid:that.openId, 
			},
			success:function(res){
				let dat=res.data.data;
				console.log(res.data.data);
				that.userCode=dat.username;
				that.userPWord=dat.passwd;
			}
		});
		uni.showLoading({
		    title: '账单查询中'
		});
		
	},
	onShow:function(){
		// this.address=uni.getStorageSync("adress");
		let that=this;
		setTimeout(function(){
			uni.request({
				url:"https://www.ccweb.xyz/gas/wx/api/sfapi",
				data:{
					userCode:that.userCode,
					userPWord:that.userPWord,
					tradeCode:"0001",
				},
				header: {
					    'Content-Type': 'application/x-www-form-urlencoded'
					     },
				method: 'POST',
				success:function(res){
					that.bills=res.data.info[0].bills;
					that.user=res.data.info[0].user[0];
					console.log(res.data);
					uni.hideLoading();
				
				}
			})
		},500);
		
	},
	methods:{
		toPay:function(){
			uni.navigateTo({
				url: 'payment'
			});
		},
		viewHistory:function(){
			uni.navigateTo({
				url: 'viewHistory'
			});
		}
	}
}
